import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import { gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
import FormSaldoAwalCT from "./Component/FormSaldoAwalCT";

const serverMaster = process.env.REACT_APP_BACKEND_URL;
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
class SaldoAwalCT extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tipeTransksi: "HUTANG",
      listSales: [],
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
    Axios.get(serverMaster + "storages2?tipeData=SALES")
      .then((res) =>
        this.setState({
          listSales: res.data,
        })
      )
      .catch(() => gagal("Mohon periksa koneksi anda"));
  }
  handleChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handlerSimpan(data) {
    console.log(data);
    var isi = {
      kodeLokasi: "-",
      namaToko: "-",
      kodeSales: "-",
      kodeCustomer: "-",
      noBon: this.props.noFaktur,
      noFaktur: this.props.noFaktur,
      tanggal: data.tanggal,
      tipeTransaksi: "SALDO-AWAL-CK/RSK",
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
      footerItem: {
        totalP: 0,
        totalK: 0,
        totalpkg: 0,
        totalNettoExt: 0,
        totalBruto: data.bruto || 0,
        totalNetto: data.netto || 0,
        totalGross: 0,
        totalQty: 0,
        totalRp: 0,
      },
    };
    console.log(JSON.stringify(isi));
    var hasil = JSON.parse(localStorage.getItem("SaldoAwalCT")) || [];
    hasil.push(isi);
    localStorage.setItem("SaldoAwalCT", JSON.stringify(hasil));
    // Axios.post(server + "spp/dc/trm", isi)
    //   .then(() => berhasil("Saldo Awal Berhasil Disimpan"))
    //   .then(() => localStorage.removeItem("noFaktur"))
    //   .then(() => this.props.dispatch(getUUID()))
    //   .catch(() =>
    //     gagal("Sepertinya koneksi anda bermasalah, Mohon periksa koneksi anda")
    //   );
  }
  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/saldoawal">
            <button type="button" className="btn btn-secondary">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-lg-12">
            <div className="card">
              <HeaderCard title="Saldo Awal Piutang Customer" />
              <div className="card-body">
                <div className="col-lg-12 mt-3">
                  <FormSaldoAwalCT
                    onSubmit={(data) => this.handlerSimpan(data)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

SaldoAwalCT = reduxForm({
  form: "SaldoAwalCT",
  enableReinitialize: true,
})(SaldoAwalCT);
export default connect(maptostate, null)(SaldoAwalCT);
