import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { gagal } from "../../Alert";
import { AxiosMasterGet } from "../../Axios";
import { renderField } from "../../renderField";

class FormSaldoAwalCT extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("subcategorys2?kodeKelompok=CIOK")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch(() =>
        gagal("Sepertinya koneksi bermasalah, mohon periksa koneksi anda")
      );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              label="Tanggal"
              name="tanggal"
              component={renderField}
              type="date"
            />
          </div>
          <div className="col-lg-3">
            <label htmlFor="">Kode Barang</label>
            <Field
              label="Kode Barang"
              name="kodeKategori"
              component="select"
              className="form-control"
            >
              <option value="DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.state.listSupplier.map((list) => (
                <option value={list.kodeJenis} key={list.kodeKategori}>
                  {list.namaJenis}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-3">
            <Field
              label="Netto"
              name="netto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-3">
            <Field
              label="Bruto"
              name="bruto"
              component={renderField}
              type="number"
            />
          </div>
          <div className="col-lg-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormSaldoAwalCT = reduxForm({
  form: "FormSaldoAwalCT",
  enableReinitialize: true,
})(FormSaldoAwalCT);
export default connect()(FormSaldoAwalCT);
